const ProgressPage: React.FC = () => {
  const [showModal, setShowModal] = React.useState(false); // Estado para mostrar el modal
  const [currentLevel, setCurrentLevel] = React.useState<"Principiante" | "Intermedio" | "Avanzado">("Principiante"); // Nivel actual

  return (
    <div className="p-6">
      {/* Contenido principal */}
      <UserProgress
        username="Camila G贸mez"
        levels={[
          {
            id: 1,
            label: "Principiante",
            completedIcon: "/assets/level-1-completed.png",
            lockedIcon: undefined,
            state: "completed",
          },
          {
            id: 2,
            label: "Intermedio",
            completedIcon: "/assets/level-2-completed.png",
            lockedIcon: "/assets/level-2-locked.png",
            state: "in-progress",
          },
          {
            id: 3,
            label: "Avanzado",
            completedIcon: "/assets/level-3-completed.png",
            lockedIcon: "/assets/level-3-locked.png",
            state: "locked",
          },
        ]}
      />

      <h1 className="text-2xl font-bold text-primary-900 mb-6">Progreso</h1>

      {/* M贸dulos */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {levels.map((level) => (
          <div key={level.id} className="flex flex-col gap-4">
            <h2 className="text-lg font-bold text-primary P16">{level.title}</h2>
            <p className="text-sm text-neutral-600">{level.subtitle}</p>
            <div className="flex flex-col gap-2">
              {level.modules.map((module) => (
                <ModuleCard
                  key={module.id}
                  title={module.title}
                  status={module.status}
                  href={
                    module.status !== "locked"
                      ? `/dashboard/progress/module/${module.id}`
                      : undefined
                  }
                />
              ))}
            </div>

            {/* Texto y bot贸n de examen */}
            <div className="mt-6 flex flex-col items-center">
              <p className="p16 text-neutral-600 mb-4">
                {examText(level.title, allModulesCompleted, isExamCompleted)}
              </p>
              <ExamButton
                isDisabled={!allModulesCompleted || isExamCompleted}
                onClick={() => {
                  if (allModulesCompleted && !isExamCompleted) {
                    setCurrentLevel(level.title); // Actualiza el nivel actual
                    setShowModal(true); // Muestra el modal
                    setIsExamCompleted(true); // Marca el examen como completado
                  }
                }}
              >
                Comenzar examen
              </ExamButton>
            </div>
          </div>
        ))}
      </div>

      {/* Modal de felicitaciones */}
      {showModal && (
        <LevelCompletionModal
          level={currentLevel}
          username="Camila G贸mez"
          onClose={() => setShowModal(false)}
        />
      )}
    </div>
  );
};

export default ProgressPage;
